.container {
  width: 1400px !important;
  margin: 0 auto;
  padding: 0;
}

body {
  font-family: 'Noto Sans KR', sans-serif;
}

header {
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  z-index: 11;
  background-color: #0f152e;
}

header.gnbOn {
  background-color: #fff;
}

header .container .row {
  height: 100px;
  align-items: center;
  justify-content: space-between;
}

header .container .row h1 {
  margin: 0 !important;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}

header .container .row .logo {
  height: 30px;
}

header .container .row .xi-bars, header .container .row .xi-close {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  font-size: 50px;
  cursor: pointer;
  color: #fff;
}

header .container .row .xi-close {
  color: #333;
}

header #gnb {
  display: none;
  position: absolute;
  top: 100px;
  height: 580px;
  text-align: center;
  background-color: rgba(255, 255, 255, 0.9);
  align-items: center;
}

header #gnb ul {
  align-items: center;
}

header #gnb ul li {
  font-size: 48px;
}

.container {
  width: 1400px !important;
  margin: 0 auto;
  padding: 0;
}

body {
  font-family: 'Noto Sans KR', sans-serif;
}

#mainBanner {
  height: 600px;
  background-color: #0f152e;
}

#mainBanner .row {
  justify-content: space-between;
  align-items: center;
}

#mainBanner .row .textArea {
  padding: 0;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  color: #fff;
}

#mainBanner .row img {
  width: auto;
  height: 500px;
}

#main #itemList {
  text-align: center;
  padding: 100px 0;
}

#main #itemList h2 {
  margin-bottom: 30px;
}

#main #itemList p {
  margin-bottom: 80px;
}

#main #itemList #listSlide {
  position: relative;
}

#main #itemList #listSlide .container {
  overflow: hidden;
}

#main #itemList #listSlide .swiper-slide {
  cursor: pointer;
  width: 450px;
  height: 300px;
  box-sizing: border-box !important;
  text-align: right;
  border: 1px solid #666;
  padding: 50px 30px;
}

#main #itemList #listSlide .swiper-slide i {
  font-style: normal;
  font-size: 16px;
  color: #666;
}

#main #itemList #listSlide .swiper-slide h3 {
  margin-top: 10px;
  font-size: 30px;
  font-weight: 600;
}

#main #itemList #listSlide .swiper-slide .row {
  justify-content: space-between;
  align-items: flex-end;
}

#main #itemList #listSlide .swiper-slide .row img {
  width: auto;
  height: 140px;
}

#main #itemList #listSlide .swiper-slide .row .listNum {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  font-size: 72px;
  font-weight: bold;
  margin: 0;
}

#main #itemList #listSlide .swiper-slide-active {
  border: 10px solid #ec6718;
  padding: 40PX 20PX;
}

#main #itemList #listSlide .swiper-slide-active .listNum {
  color: #ec6718;
}

#main #itemList #listSlide .swiper-button-prev, #main #itemList #listSlide .swiper-button-next {
  width: 70px;
  height: 70px;
  margin: 0;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  background-position: center;
  background-size: cover;
}

#main #itemList #listSlide .swiper-button-prev::before, #main #itemList #listSlide .swiper-button-prev:after, #main #itemList #listSlide .swiper-button-next::before, #main #itemList #listSlide .swiper-button-next:after {
  display: none;
}

#main #itemList #listSlide .swiper-button-prev {
  left: 70px !important;
}

#main #itemList #listSlide .swiper-button-next {
  right: 70px !important;
}

#main #request {
  background-color: #0f152e;
  height: 200px;
}

#main #request .container {
  position: relative;
  height: 100%;
}

#main #request .container .row {
  align-items: center;
  justify-content: space-around;
}

#main #request .container .row h2 {
  display: block;
  font-size: 32px;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  color: #fff;
  padding: 0;
}

#main #request .container .row form {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  padding: 0;
}

#main #request .container .row form input[type='text'] {
  width: 180px;
  margin-right: 20px;
  padding: 5px;
  border: 3px solid #999;
  box-sizing: border-box;
}

#main #request .container .row form label {
  color: #999;
  margin-right: 70px;
  font-size: 15px;
}

#main #request .container .row form input[type='submit'] {
  border: 10px solid #ec6718;
  background-color: #fff;
  color: #ec6718;
  font-size: 32px;
  font-weight: 600;
  width: 200px;
  height: 200px;
  margin: 0;
}

#main #request .container span {
  position: absolute;
  bottom: 15px;
  color: #fff;
  width: 100%;
  text-align: center;
  font-size: 14px;
}

